.header {
  background-color: #4169e1;
}

.container {
  text-align: center;
  width: 1200px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 10px 15px;
  display: flex;
}

.submit-button {
  cursor: pointer;
  color: orange;
  letter-spacing: .06em;
  height: 40px;
  background-color: #000;
  border: none;
  border-radius: 4px;
  margin-left: 20px;
  padding: 10px 32px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.input {
  height: 40px;
  width: 60%;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: none;
  padding-left: 20px;
}

.search-form {
  width: 600px;
  align-items: center;
  display: flex;
}

.info-item {
  text-decoration: none;
  list-style: none;
}

.load-more {
  height: 30px;
  border-radius: 5px;
  margin: 30px auto;
  font-size: 20px;
  display: block;
}

.load-more:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

.load-more.hidden {
  display: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 15px;
  display: flex;
}

.photo-card {
  width: calc(25% - 30px);
  margin: 0;
  overflow: hidden;
}

.photo-card img {
  width: 410px;
  height: 250px;
  object-fit: cover;
}

.info {
  justify-content: center;
  align-items: center;
  font-size: 12px;
  display: flex;
}

.info-item {
  margin-left: 10px;
  margin-right: 10px;
}

